import React, { useEffect, useState } from "react";
import axios from "axios";
import * as FileSaver from "file-saver";
import * as XLSX from "xlsx";

const ApiRequest = () => {
  const [referenceNumber, setReferenceNumber] = useState("");
  const [responses, setResponses] = useState([]);
  const [fetchingData, setFetchingData] = useState(false);
  const [inputError, setInputError] = useState("");

  console.log({ responses });
  const parseResponseData = (doc, isGeneral) => {
    if (!doc || !doc.querySelector) {
      return null;
    }

    const getTextContent = (selector) =>
      doc.querySelector(selector)?.textContent.replace(/\s+/g, " ").trim() ||
      "";

    const combinedAddress = isGeneral
      ? getTextContent(
          "body > div.maincontent.fontsize > table:nth-child(5) > tbody > tr > td.border-r > table > tbody > tr:nth-child(2) > td:nth-child(1)"
        ).split("NAME & ADDRESS ")[1]
      : [
          getTextContent(
            "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(5) > td:nth-child(1) > table > tbody > tr:nth-child(2) > td"
          ),
          getTextContent(
            "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(5) > td:nth-child(1) > table > tbody > tr:nth-child(3) > td"
          ),
        ]
          .join(", ")
          .split("GST #")[0]
          .trim();

    const ntnSelectorText = isGeneral
      ? "body > div.maincontent.fontsize > table:nth-child(5) > tbody > tr > td.border-r > table > tbody > tr:nth-child(3) > td:nth-child(1) > div"
      : "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(5) > td:nth-child(2)";
    const ntnSelector = isGeneral
      ? getTextContent(ntnSelectorText).split("NTN No")[1]
      : getTextContent(ntnSelectorText).match(/NTN # (\d+)/) || [];

    const referenceNoSelector = isGeneral
      ? "body > div.maincontent.fontsize > div:nth-child(3) > table > tbody > tr:nth-child(4) > td:nth-child(1)"
      : "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(4) > td.border-rb.bodyContentValue";

    const gstNoSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.header > div.heading > h1 > span"
      : "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(5) > td:nth-child(1) > table > tbody > tr:nth-child(3) > td > b";

    const billMonthSelector = isGeneral
      ? "body > div.maincontent.fontsize > table:nth-child(2) > tbody > tr.content > td:nth-child(4)"
      : "body > div.maincontent > div.header > div > table > tbody > tr.bodyContentValue > td:nth-child(5)";
    const gstSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(1) > tbody > tr:nth-child(4) > td:nth-child(4)"
      : "body > div.maincontent > div:nth-child(3) > table:nth-child(1) > tbody > tr:nth-child(4) > td:nth-child(4)";
    const incomeTaxSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(1) > tbody > tr:nth-child(5) > td:nth-child(4)"
      : "body > div.maincontent > div:nth-child(3) > table:nth-child(1) > tbody > tr:nth-child(5) > td:nth-child(4)";
    const extraTaxSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(1) > tbody > tr:nth-child(6) > td:nth-child(4)"
      : "body > div.maincontent > div:nth-child(3) > table:nth-child(1) > tbody > tr:nth-child(6) > td:nth-child(4)";
    const furtherTaxSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(1) > tbody > tr:nth-child(7) > td:nth-child(4)"
      : "body > div.maincontent > div:nth-child(3) > table:nth-child(1) > tbody > tr:nth-child(7) > td:nth-child(4)";
    const sTaxSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(1) > tbody > tr:nth-child(9) > td:nth-child(4)"
      : "body > div.maincontent > div:nth-child(3) > table:nth-child(1) > tbody > tr:nth-child(9) > td:nth-child(4)";
    const arrearSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(2) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(1) > td.nestedtd2width.bodyContentValue";
    const currentBillSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(3) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(2) > td.nestedtd2width.bodyContentValue";
    const billAdjustmentSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(4) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(3) > td.nestedtd2width.bodyContentValue";
    const installmentSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(5) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(4) > td.nestedtd2width.bodyContentValue";
    const totalFPASelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(7) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(5) > td.nestedtd2width.bodyContentValue > div:nth-child(1)";
    const payableWithinDueDateSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(8) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(7) > td.nestedtd2width.bodyContentValue";
    const lPSurchargeSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(9) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(8) > td.nestedtd2width.bodyContentValue";
    const payableAfterDueDateSelector = isGeneral
      ? "body > div.maincontent.fontsize > div.border-t > table:nth-child(2) > tbody > tr:nth-child(10) > td.border-b.nestedtd2width.content"
      : "body > div.maincontent > div:nth-child(3) > table.nested7.bordertop > tbody > tr:nth-child(9) > td.nestedtd2width.bodyContentValue.border-bt";
    const issueDateSelector = isGeneral
      ? "body > div.maincontent.fontsize > table:nth-child(2) > tbody > tr.content > td:nth-child(6)"
      : "body > div.maincontent > div.header > div > table > tbody > tr.bodyContentValue > td:nth-child(7)";
    const dueDateSelector = isGeneral
      ? "body > div.maincontent.fontsize > table:nth-child(2) > tbody > tr.content > td.border-b"
      : "body > div.maincontent > div.header > div > table > tbody > tr.bodyContentValue > td:nth-child(8)";
    const consumerIdSelector = isGeneral
      ? "body > div.maincontent.fontsize > div:nth-child(3) > table > tbody > tr:nth-child(2) > td:nth-child(1)"
      : "body > div.maincontent > table > tbody > tr:nth-child(1) > td.border-rtop > table > tbody > tr:nth-child(2) > td:nth-child(1)";

    return {
      referenceNo: getTextContent(referenceNoSelector).replace(/\s/g, ""),
      address: combinedAddress,
      ntn: isGeneral
        ? ntnSelector
        : ntnSelector[0]
        ? ntnSelector[0].match(/\d+/g)?.join("")
        : "",
      gstNo: getTextContent(gstNoSelector).match(/\d+/g)?.join("") || "",
      billMonth: getTextContent(billMonthSelector),
      gst: getTextContent(gstSelector),
      incomeTax: getTextContent(incomeTaxSelector).split(" ")[0],
      extraTax: getTextContent(extraTaxSelector),
      furtherTax: getTextContent(furtherTaxSelector),
      sTax: getTextContent(sTaxSelector),
      arrear: getTextContent(arrearSelector),
      currentBill: getTextContent(currentBillSelector),
      billAdjustment: getTextContent(billAdjustmentSelector),
      installment: getTextContent(installmentSelector),
      totalFPA: getTextContent(totalFPASelector),
      payableWithinDueDate: getTextContent(payableWithinDueDateSelector),
      lPSurcharge: getTextContent(lPSurchargeSelector),
      payableAfterDueDate: getTextContent(payableAfterDueDateSelector),
      issueDate: getTextContent(issueDateSelector),
      dueDate: getTextContent(dueDateSelector),
      consumerId: getTextContent(consumerIdSelector),
    };
  };
  const fetchData = async () => {
    const uniqueRefs = referenceNumber
      .split(",")
      .map((ref) => ref.trim())
      .filter((ref) => ref);
    const refs = Array.from(new Set(uniqueRefs));

    if (refs.length === 0) {
      setInputError("Please enter one or more reference numbers");
      return;
    }

    if (refs.some((ref) => !/^\d{14,}$/.test(ref))) {
      setInputError(
        "Please enter valid reference numbers (each at least 14 digits)"
      );
      return;
    }
   const isValid = responses?.filter(
      (item) => item?.referenceNo?.slice(0, -1) === referenceNumber
    );

    console.log({refs})

    if (isValid?.length !== 0) {
      setInputError(
        "Please Enter other reference number because already exist"
      );
      setFetchingData(false);
    }
    // if (!referenceNumber.trim()) {
    //   setInputError("Please enter a reference number");
    //   return;
    // } else if (!/^\d{14,}$/.test(referenceNumber)) {
    //   setInputError(
    //     "Please enter a valid reference number (at least 14 digits)"
    //   );
    //   return;
    // }

    setInputError("");
    setFetchingData(true);
    // setResponses([]);

    try {
      const host = `https://bill.pitc.com.pk/`;
      const apiEndpoints = [
        "fescobill/industrial",
        "gepcobill/industrial",
        "iescobill/industrial",
        "mepcobill/industrial",
        "pescobill/industrial",
        "hescobill/industrial",
        "sepcobill/industrial",
        "tescobill/industrial",

        "fescobill/general",
        "gepcobill/general",
        "iescobill/general",
        "mepcobill/general",
        "pescobill/general",
        "hescobill/general",
        "sepcobill/general",
        "tescobill/general",
      ];
      // const allResponses = [];
      for (const ref of refs) {
        const fetchPromises = apiEndpoints.map((endpoint) =>
          axios.get(`${host}${endpoint}?refno=${ref}`).catch(() => null)
        );

        const apiResponses = await Promise.all(fetchPromises);
        const validResponses = apiResponses.filter(
          (response) =>
            response &&
            response.status === 200 &&
            response.data.trim() !== "" &&
            !response.data.includes("Bill Not Found!")
        );

        const newResponses = validResponses
          .map((response) => {
            const doc = new DOMParser().parseFromString(
              response.data,
              "text/html"
            );
            const isGeneral = response.config.url.includes("/general");
            return parseResponseData(doc, isGeneral);
          })
          .filter((response) => response !== null);

        setResponses((prev) => [...newResponses, ...prev]);
        setReferenceNumber("");
      }
      // setResponses(allResponses);
    } catch (error) {
      console.error("Error:", error.message);
    } finally {
      setFetchingData(false);
    }
  };

  const saveToExcel = () => {
    const fileType =
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
    const fileExtension = ".xlsx";
    const ws = XLSX.utils.json_to_sheet(responses);
    const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
    const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, "Bill Response Data" + fileExtension);
  };

  return (
    <>
      <div>
        <label htmlFor="referenceInput">Enter Reference Number:</label>
        {/* <input
          type="text"
          id="referenceInput"
          required
          placeholder="Enter reference number"
          value={referenceNumber}
          onChange={(e) => setReferenceNumber(e.target.value)}
        /> */}
        <input
          type="text"
          id="referenceInput"
          required
          placeholder="Enter reference numbers separated by commas"
          value={referenceNumber}
          onChange={(e) => setReferenceNumber(e.target.value)}
        />
        {inputError && (
          <div style={{ color: "red", textAlign: "center" }}>{inputError}</div>
        )}
        <button onClick={fetchData} disabled={fetchingData}>
          {fetchingData ? "Fetching..." : "Fetch Data"}
        </button>
        {responses.length > 0 && (
          <button onClick={saveToExcel}>Download Excel</button>
        )}
        {responses.length > 0 && (
          <h6>
            Total Response: {responses.length === 0 ? "" : responses.length}
          </h6>
        )}

        <div id="response-container">
          {responses.length === 0 && !fetchingData && (
            <div>No valid responses received.</div>
          )}
          {responses.map((response, index) => (
            <div key={index}>
              Reference Number: {response.referenceNo}
              <br />
              Address: {response.address}
              {index < responses.length - 1 ? <hr /> : null}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default ApiRequest;
